import React from 'react';
import { createRoot } from 'react-dom/client';
// import { HashRouter } from 'react-router-dom'; // Moved to App.tsx
import './index.css';
import App from './App';

// 
console.log('React ...', process.env.NODE_ENV);
console.log('DOM root element:', document.getElementById('root'));

// React 18createRoot
const container = document.getElementById('root');
if (!container) {
  console.error('rootHTML<div id="root"></div>');
  throw new Error('Failed to find the root element');
}
const root = createRoot(container);

// GitHub PagesBrowserRouterHashRouter
// HashRouterbasenameURL
console.log('Using HashRouter for GitHub Pages compatibility');

try {
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
  console.log('React ');
} catch (error) {
  console.error('React :', error);
}