import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { 
  Container, 
  Typography, 
  Box, 
  Grid, 
  Card, 
  CardContent, 
  CardActionArea,
  TextField,
  InputAdornment,
  Chip,
  Button,
  CircularProgress,
  Pagination,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Paper,
  Autocomplete,
  Popover,
  Alert
} from '@mui/material';

import { Link as RouterLink } from 'react-router-dom';
import SearchIcon from '@mui/icons-material/Search';
import SortIcon from '@mui/icons-material/Sort';
import PublicIcon from '@mui/icons-material/Public';
import CakeIcon from '@mui/icons-material/Cake';
import PersonIcon from '@mui/icons-material/Person';
import CategoryIcon from '@mui/icons-material/Category';
import SchoolIcon from '@mui/icons-material/School';
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { ArchitectService, initDatabase, getResultsArray } from '../services/db';
import { Architect, ArchitectsResponse, TagCategory } from '../types/architect';

// 
interface SortOption {
  value: string;
  label: string;
  icon: React.ReactNode;
}

const ArchitectsPage: React.FC = () => {
  console.log("ArchitectsPage ");
  
  // 
  const [architects, setArchitects] = useState<Architect[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [totalPages, setTotalPages] = useState<number>(0);
  const [totalItems, setTotalItems] = useState<number>(0);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [selectedTags, setSelectedTags] = useState<string[]>([]);
  const [selectedTagsWithYears, setSelectedTagsWithYears] = useState<string[]>([]);
  const [availableTags, setAvailableTags] = useState<string[]>([]);
  const [sortBy, setSortBy] = useState<string>('name');
  const [sortOrder, setSortOrder] = useState<string>('asc');
  
  // 
  const [yearAnchorEl, setYearAnchorEl] = useState<HTMLElement | null>(null);
  const [currentTagForYear, setCurrentTagForYear] = useState<string>('');
  const [tagsYears, setTagsYears] = useState<Record<string, string[]>>({});
  
  // 
  const [nationality, setNationality] = useState<string>('');
  const [category, setCategory] = useState<string>('');
  const [school, setSchool] = useState<string>('');
  const [birthYearFrom, setBirthYearFrom] = useState<string>('');
  const [birthYearTo, setBirthYearTo] = useState<string>('');
  const [deathYear, setDeathYear] = useState<string>('');
  
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  
  // : 
  useEffect(() => {
    console.log("tagMenuAnchorEl :", Boolean(yearAnchorEl));
  }, [yearAnchorEl]);
  
  // 
  const sortOptions: SortOption[] = [
    { value: 'name', label: '', icon: <SortByAlphaIcon /> },
    { value: 'birthYear', label: '', icon: <CalendarMonthIcon /> },
    { value: 'nationality', label: '', icon: <PublicIcon /> }
  ];

  // 
  useEffect(() => {
    console.log("useEffect");
    const loadTags = async () => {
      try {
        console.log("");
        const tags = await ArchitectService.getArchitectTags();
        console.log(":", tags);
        
        // 
        const baseTags = ['', '', '', '', ''];
        setAvailableTags(baseTags);
      } catch (error) {
        console.error(':', error);
      }
    };
    
    loadTags();
  }, []);

  // 
  useEffect(() => {
    console.log("useEffect", location.search);
    const loadArchitects = async () => {
      setLoading(true);
      try {
        // 
        const page = parseInt(queryParams.get('page') || '1');
        const search = queryParams.get('search') || '';
        const tags = queryParams.get('tags') ? queryParams.get('tags').split(',') : [];
        const sort = queryParams.get('sortBy') || 'name';
        const order = queryParams.get('sortOrder') || 'asc';
        
        // 
        const nat = queryParams.get('nationality') || '';
        const cat = queryParams.get('category') || '';
        const sch = queryParams.get('school') || '';
        const birthFrom = queryParams.get('birthYearFrom') || '';
        const birthTo = queryParams.get('birthYearTo') || '';
        const death = queryParams.get('deathYear') || '';
        
        console.log("URL:", { page, search, tags, sort, order, nat, cat, sch, birthFrom, birthTo, death });
        
        // 
        setCurrentPage(page);
        setSearchTerm(search);
        setSelectedTags(tags);
        setSortBy(sort);
        setSortOrder(order);
        setNationality(nat);
        setCategory(cat);
        setSchool(sch);
        setBirthYearFrom(birthFrom);
        setBirthYearTo(birthTo);
        setDeathYear(death);
        
        // 
        console.log(":", { page, search, tags, sort, order, nat, cat, sch, birthFrom, birthTo, death });
        const result = await ArchitectService.getAllArchitects(
          page,
          10, // itemsPerPage
          search,
          tags,
          sort,
          order,
          nat,
          cat,
          sch,
          birthFrom ? parseInt(birthFrom) : 0,
          birthTo ? parseInt(birthTo) : 0,
          death ? parseInt(death) : 0
        );
        
        console.log(":", result);
        setArchitects(result.items);
        setTotalPages(result.totalPages);
        setTotalItems(result.total);
      } catch (error) {
        console.error(':', error);
      } finally {
        setLoading(false);
      }
    };
    
    loadArchitects();
  }, [location.search]);

  // 
  const getYearsForArchitectTag = async (tag: string) => {
    console.log(`${tag}`);
    let query = '';
    if (tag === '') {
      query = `SELECT DISTINCT ZAT_NATIONALITY as value FROM ZCDARCHITECT WHERE ZAT_NATIONALITY != '' ORDER BY ZAT_NATIONALITY`;
    } else if (tag === '') {
      query = `SELECT DISTINCT ZAT_CATEGORY as value FROM ZCDARCHITECT WHERE ZAT_CATEGORY != '' ORDER BY ZAT_CATEGORY`;
    } else if (tag === '') {
      query = `SELECT DISTINCT ZAT_SCHOOL as value FROM ZCDARCHITECT WHERE ZAT_SCHOOL != '' ORDER BY ZAT_SCHOOL`;
    } else if (tag === '') {
      query = `SELECT DISTINCT ZAT_BIRTHYEAR as value FROM ZCDARCHITECT WHERE ZAT_BIRTHYEAR > 0 ORDER BY ZAT_BIRTHYEAR DESC`;
    } else if (tag === '') {
      query = `SELECT DISTINCT ZAT_DEATHYEAR as value FROM ZCDARCHITECT WHERE ZAT_DEATHYEAR > 0 ORDER BY ZAT_DEATHYEAR DESC`;
    }
    
    if (!query) return [];
    
    try {
      // getResultsArray
      const results = await getResultsArray<{ value: string | number }>(query);
      
      // 
      return results.map(item => String(item.value)).filter(Boolean);
    } catch (error) {
      console.error(`${tag}:`, error);
      return [];
    }
  };

  // 
  const handleYearMenuOpen = async (event: React.MouseEvent<HTMLElement>, tag: string) => {
    console.log(`${tag}`, event.currentTarget);
    setYearAnchorEl(event.currentTarget);
    setCurrentTagForYear(tag);
    
    // 
    if (!tagsYears[tag]) {
      try {
        const years = await getYearsForArchitectTag(tag);
        console.log(`${tag}:`, years);
        setTagsYears(prev => ({
          ...prev,
          [tag]: years
        }));
      } catch (error) {
        console.error(`${tag}:`, error);
      }
    }
  };

  // 
  const handleYearMenuClose = () => {
    console.log("");
    setYearAnchorEl(null);
    setCurrentTagForYear('');
  };

  // 
  const handleYearSelect = (year: string) => {
    console.log(`: ${year} (: ${currentTagForYear})`);
    
    if (currentTagForYear === '') {
      setNationality(year);
    } else if (currentTagForYear === '') {
      setCategory(year);
    } else if (currentTagForYear === '') {
      setSchool(year);
    } else if (currentTagForYear === '') {
      setBirthYearFrom(year);
    } else if (currentTagForYear === '') {
      setDeathYear(year);
    }
    
    handleYearMenuClose();
    updateUrlAndSearch();
  };

  // 
  const handleTagSelect = (event: React.SyntheticEvent, newTags: string[]) => {
    console.log(`:`, newTags);
    setSelectedTags(newTags);
    
    // 
    const removedTags = selectedTags.filter(tag => !newTags.includes(tag));
    
    for (const tag of removedTags) {
      if (tag === '') {
        setNationality('');
      } else if (tag === '') {
        setCategory('');
      } else if (tag === '') {
        setSchool('');
      } else if (tag === '') {
        setBirthYearFrom('');
        setBirthYearTo('');
      } else if (tag === '') {
        setDeathYear('');
      }
    }
    
    // 
    const addedTags = newTags.filter(tag => !selectedTags.includes(tag));
    
    if (addedTags.length > 0) {
      addedTags.forEach(async (tag) => {
        if (!tagsYears[tag]) {
          try {
            const years = await getYearsForArchitectTag(tag);
            setTagsYears(prev => ({
              ...prev,
              [tag]: years
            }));
          } catch (error) {
            console.error(`${tag}:`, error);
          }
        }
      });
    }
    
    updateUrlAndSearch();
  };

  // 
  const handleSortChange = (sortValue: string) => {
    console.log(`: ${sortValue}`);
    
    // /
    if (sortBy === sortValue) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortBy(sortValue);
      setSortOrder('asc');
    }
    
    updateUrlAndSearch();
  };

  // 
  const handlePageChange = (event: React.ChangeEvent<unknown>, value: number) => {
    console.log(`: ${value}`);
    setCurrentPage(value);
    
    const newParams = new URLSearchParams(location.search);
    newParams.set('page', value.toString());
    navigate({ search: newParams.toString() });
  };

  // 
  const handleSearch = () => {
    console.log(`: ${searchTerm}`);
    updateUrlAndSearch();
  };

  // 
  const handleClearSearch = () => {
    console.log("");
    setSearchTerm('');
    setSelectedTags([]);
    setSelectedTagsWithYears([]);
    setNationality('');
    setCategory('');
    setSchool('');
    setBirthYearFrom('');
    setBirthYearTo('');
    setDeathYear('');
    setCurrentPage(1);
    
    // URL
    const queryParams = new URLSearchParams();
    if (sortBy !== 'name') {
      queryParams.set('sortBy', sortBy);
    }
    if (sortOrder !== 'asc') {
      queryParams.set('sortOrder', sortOrder);
    }
    navigate({ search: queryParams.toString() });
  };

  // URL
  const updateUrlAndSearch = () => {
    console.log("URL");
    const newQueryParams = new URLSearchParams();
    
    if (currentPage > 1) newQueryParams.set('page', currentPage.toString());
    if (searchTerm) newQueryParams.set('search', searchTerm);
    if (selectedTags.length > 0) newQueryParams.set('tags', selectedTags.join(','));
    if (sortBy !== 'name') newQueryParams.set('sortBy', sortBy);
    if (sortOrder !== 'asc') newQueryParams.set('sortOrder', sortOrder);
    if (nationality) newQueryParams.set('nationality', nationality);
    if (category) newQueryParams.set('category', category);
    if (school) newQueryParams.set('school', school);
    if (birthYearFrom) newQueryParams.set('birthYearFrom', birthYearFrom);
    if (birthYearTo) newQueryParams.set('birthYearTo', birthYearTo);
    if (deathYear) newQueryParams.set('deathYear', deathYear);
    
    console.log("URL:", newQueryParams.toString());
    navigate({ search: newQueryParams.toString() });
  };

  // 
  const CurrentFilters = () => {
    const activeFilters = [];
    
    if (nationality) activeFilters.push({ label: `: ${nationality}`, onDelete: () => { setNationality(''); updateUrlAndSearch(); } });
    if (birthYearFrom) activeFilters.push({ label: `: ${birthYearFrom}`, onDelete: () => { setBirthYearFrom(''); updateUrlAndSearch(); } });
    if (deathYear) activeFilters.push({ label: `: ${deathYear}`, onDelete: () => { setDeathYear(''); updateUrlAndSearch(); } });
    if (category) activeFilters.push({ label: `: ${category}`, onDelete: () => { setCategory(''); updateUrlAndSearch(); } });
    if (school) activeFilters.push({ label: `: ${school}`, onDelete: () => { setSchool(''); updateUrlAndSearch(); } });
    
    if (activeFilters.length === 0 && !searchTerm && selectedTags.length === 0) return null;
    
    return (
      <Box sx={{ mb: 3 }}>
        <Alert 
          severity="info" 
          action={
            <Button color="inherit" size="small" onClick={handleClearSearch}>
              
            </Button>
          }
        >
          {activeFilters.length > 0 
            ? `: ${activeFilters.map(f => f.label).join(', ')}` 
            : searchTerm 
              ? `${searchTerm}` 
              : selectedTags.length > 0 
                ? `${selectedTags.join(', ')}`
                : ''}
        </Alert>
      </Box>
    );
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        
      </Typography>

      <Paper sx={{ p: 3, mb: 4 }}>
        {/*  */}
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 2, mb: 3 }}>
          <TextField
            fullWidth
            label=""
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
          <Button 
            variant="contained" 
            onClick={handleSearch}
            sx={{ minWidth: '120px' }}
          >
            
          </Button>
        </Box>
        
        {/*  */}
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 2, mb: 3, alignItems: 'flex-start' }}>
          {/*  */}
          <Autocomplete
            multiple
            id="tags-selector"
            options={availableTags}
            value={selectedTags}
            onChange={handleTagSelect}
            fullWidth
            renderInput={(params) => (
              <TextField
                {...params}
                variant="outlined"
                label=""
                placeholder=""
              />
            )}
            renderTags={(value, getTagProps) =>
              value.map((option, index) => {
                // 
                const isExpandable = Boolean(tagsYears[option] && tagsYears[option].length > 0);
                
                // 
                let selectedValue = '';
                if (option === '') selectedValue = nationality;
                else if (option === '') selectedValue = category;
                else if (option === '') selectedValue = school;
                else if (option === '') selectedValue = birthYearFrom;
                else if (option === '') selectedValue = deathYear;
                
                return (
                  <Chip
                    key={option}
                    label={
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        {option}
                        {selectedValue && (
                          <Typography 
                            variant="caption" 
                            sx={{ 
                              ml: 0.5, 
                              fontWeight: 'bold',
                              backgroundColor: 'rgba(0, 0, 0, 0.08)',
                              borderRadius: '4px',
                              px: 0.5,
                              py: 0.2
                            }}
                          >
                            {selectedValue}
                          </Typography>
                        )}
                        {isExpandable && (
                          <ArrowDropDownIcon fontSize="small" />
                        )}
                      </Box>
                    }
                    {...getTagProps({ index })}
                    color={selectedValue ? "primary" : "default"}
                    variant="outlined"
                    onClick={(event) => {
                      // 
                      event.stopPropagation();
                      handleYearMenuOpen(event, option);
                    }}
                    onDelete={(event) => {
                      // 
                      getTagProps({ index }).onDelete(event);
                    }}
                    sx={{
                      maxWidth: '100%',
                      '& .MuiChip-label': {
                        overflow: 'hidden',
                        whiteSpace: 'nowrap',
                      }
                    }}
                  />
                );
              })
            }
          />
          
          {/*  */}
          <FormControl sx={{ minWidth: 200 }}>
            <InputLabel id="sort-select-label"></InputLabel>
            <Select
              labelId="sort-select-label"
              value={`${sortBy}_${sortOrder}`}
              label=""
              onChange={(e) => {
                const [newSortBy, newSortOrder] = e.target.value.split('_');
                setSortBy(newSortBy);
                setSortOrder(newSortOrder);
                updateUrlAndSearch();
              }}
            >
              <MenuItem value="name_asc"> ()</MenuItem>
              <MenuItem value="name_desc"> ()</MenuItem>
              <MenuItem value="birthYear_asc"> ()</MenuItem>
              <MenuItem value="birthYear_desc"> ()</MenuItem>
              <MenuItem value="nationality_asc"> ()</MenuItem>
              <MenuItem value="nationality_desc"> ()</MenuItem>
            </Select>
          </FormControl>
        </Box>
        
        {/*  */}
        <Popover
          open={Boolean(yearAnchorEl)}
          anchorEl={yearAnchorEl}
          onClose={handleYearMenuClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'left',
          }}
        >
          <Box sx={{ p: 2, width: 300, maxHeight: 400, overflow: 'auto' }}>
            <Typography variant="subtitle1" gutterBottom>
              {currentTagForYear === '' ? '' :
               currentTagForYear === '' ? '' :
               currentTagForYear === '' ? '' :
               currentTagForYear === '' ? '' :
               currentTagForYear === '' ? '' : ''}
            </Typography>
            
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mt: 1 }}>
              <Chip
                label=""
                onClick={() => handleYearSelect('')}
                variant="outlined"
                size="small"
                sx={{ mb: 1 }}
              />
              
              {tagsYears[currentTagForYear]?.map(year => (
                <Chip
                  key={year}
                  label={year}
                  onClick={() => handleYearSelect(year)}
                  variant="outlined"
                  size="small"
                  color={
                    (currentTagForYear === '' && year === nationality) ||
                    (currentTagForYear === '' && year === category) ||
                    (currentTagForYear === '' && year === school) ||
                    (currentTagForYear === '' && year === birthYearFrom) ||
                    (currentTagForYear === '' && year === deathYear)
                      ? "primary"
                      : "default"
                  }
                  sx={{ 
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    maxWidth: '100%'
                  }}
                />
              ))}
            </Box>
          </Box>
        </Popover>
        
        {/*  */}
        <CurrentFilters />

        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="body2" color="text.secondary">
            {totalItems}
          </Typography>
          {totalPages > 1 && (
            <Pagination 
              count={totalPages} 
              page={currentPage} 
              onChange={handlePageChange} 
              color="primary"
              size="small"
            />
          )}
        </Box>
      </Paper>

      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          {architects.length === 0 ? (
            <Box sx={{ textAlign: 'center', py: 4 }}>
              <Typography variant="h6">
                
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                
              </Typography>
            </Box>
          ) : (
            <Grid container spacing={3}>
              {architects.map((architect) => (
                <Grid item xs={12} sm={6} md={4} key={architect.id}>
                  <Card 
                    sx={{ 
                      height: '100%', 
                      display: 'flex', 
                      flexDirection: 'column',
                      transition: 'transform 0.2s',
                      '&:hover': {
                        transform: 'scale(1.02)',
                      },
                    }}
                  >
                    <CardActionArea component={RouterLink} to={`/architects/${architect.id}`}>
                      <CardContent>
                        <Typography variant="h6" component="div" gutterBottom>
                          {architect.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary" gutterBottom>
                          {architect.nationality}  {architect.birthYear || '?'}-{architect.deathYear || ''}
                        </Typography>
                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mt: 1 }}>
                          {architect.tags && architect.tags.map((tag) => (
                            <Chip
                              key={tag}
                              label={tag}
                              size="small"
                              variant="outlined"
                              sx={{ pointerEvents: 'none' }}
                            />
                          ))}
                        </Box>
                      </CardContent>
                    </CardActionArea>
                  </Card>
                </Grid>
              ))}
            </Grid>
          )}
          
          {/*  */}
          {totalPages > 1 && (
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
              <Pagination 
                count={totalPages} 
                page={currentPage} 
                onChange={handlePageChange} 
                color="primary"
              />
            </Box>
          )}
        </>
      )}
    </Container>
  );
};

export default ArchitectsPage; 